import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from "./Context/Theme/Theme"
import { Provider as MenuProvider } from "./Context/MenuBar/Menu"
import { Provider as VideoProvider } from "./Context/Video/Video"

import { BrowserRouter } from 'react-router-dom'
import App from './App';

ReactDOM.render(
  <React.StrictMode>
    <BrowserRouter>
      <Provider>
        <MenuProvider>
          <VideoProvider>
            <App />
          </VideoProvider>
        </MenuProvider>
      </Provider>
    </BrowserRouter>
  </React.StrictMode>,
  document.getElementById('root')
);
